  margin: 0px;
  width: 18px;
  height: 18px;
}

.mljar-packages-manager-info-icon-container {
  visibility: hidden;
  width: 150px;
  background-color: #28a745;
  color: white;
  text-align: center;
  border-radius: 4px;
  padding: 5px;
  position: absolute;
  left: -160px;
  top: 100%;
  z-index: 1;
  opacity: 0;
  transition: opacity 0.3s;
  white-space: pre-line;
}

.mljar-packages-manager-info-icon-container:hover {
  visibility: visible;
  opacity: 1;
}

.mljar-packages-manager-install-form {
  display: flex;
  flex-direction: column;
  margin-right: 8px;
}

.mljar-packages-manager-install-form h4 {
  margin-top: 0px;
  margin-bottom: 4px;
  padding: 0;
}

.mljar-packages-manager-usage-span {
  margin-bottom: 8px;
  text-align: left;
  font-size: 0.8rem;
  padding: 5px 2px;
}

.mljar-packages-manager-install-message {
  color: #28a745;
  font-weight: bold;
  margin-top: 10px;
  text-align: left;
  padding: 5px 2px;
}

.mljar-packages-manager-install-message.error {
  color: #dc3545;
}

.mljar-packages-manager-error-message {
  color: #dc3545;
  font-weight: bold;
}

.mljar-packages-manager-spinner-container {
